import React, { useState, Fragment } from 'react';
import { Route, Switch } from 'react-router-dom';
import axios from 'axios';

// My Imported Resources
import Navbar from './components/layout/Navbar';
import About from './components/layout/About';
import Footer from './components/layout/Footer';
import Contact from './components/pages/Contact';
import Repos from './components/repos/Repos';

// Styles Sheet
import './App.scss';


const App = () => {

  // useState call
  const [repos, setRepos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  // Darkmode Function
  const toggleDarkMode = () => {
    setDarkMode(!darkMode)
    if(darkMode) {
      document.getElementById("root").style.backgroundColor = "#ffffff";
      document.getElementById("root").style.color = "#333333";
      document.querySelector("#navbar .a").classList.remove("a-styles");
      document.querySelector("#navbar .b").classList.remove("a-styles");
    } else {
      document.getElementById("root").style.backgroundColor = "#333333";
      document.getElementById("root").style.color = "#ffffff";
      document.querySelector("#navbar .a").classList.add("a-styles");
      document.querySelector("#navbar .b").classList.add("a-styles");
    }
  }

  // Get Github Repos
  const getGithubRepos = async () => {
    setLoading(true)

    const res = await axios.get(`https://api.github.com/users/mahdiabubakar/repos?per_page=10&sort=created:asc?client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`);

    setRepos(res.data);
    setLoading(false);
  }
  
  return (
    <React.Fragment>
      <div className="container">
        <Navbar 
          toggleDarkMode={toggleDarkMode} 
          darkMode={darkMode}
          getGithubRepos={getGithubRepos} 
        />
        <Switch>
          <Route exact path='/' component={About} />
            <Route exact path='/contact' render={props => (
              <Fragment>
                <Contact />
                <Repos repos={repos} loading={loading} />
              </Fragment>
            )}
          />
        </Switch>
      </div>
      <Footer />
    </React.Fragment>
  );
}

export default App;
