import React, { useEffect } from 'react'
import { Link } from 'react-router-dom';
import mahdiImg from '../../img/mahdi.jpg';
import PropTypes from 'prop-types';


const Navbar = ({ toggleDarkMode, darkMode, getGithubRepos }) => {
    useEffect(async () => {
        getGithubRepos();
        // eslint-diasble-next-line
    }, [])

    return (
        <div className="navbar">
            <div className="navbar-img">
                <img src={mahdiImg} alt="Mahdi Abubakar" className="round-img img" style={{ width: '200px' }} />
            </div>
            <div className="navbar-links">
                <p onClick={toggleDarkMode} className="night-mode"> <span style={{ fontSize: "1rem", paddingLeft: "10px" }}>{ darkMode ? "Light Mode" : "Dark Mode" }</span>
                    {darkMode ? <i className="fas fa-sun"></i> : <i className="fas fa-moon"></i> }
                </p>
                <h1>Mahdi Abubakar</h1>
                <p style={styles}>nigeriancoder</p>
                <ul id="navbar">
                    <li>
                        <Link className="a" to='/'>About</Link>
                    </li>
                    {/* <li>
                        <Link to='/blog'>Blog</Link>
                    </li> */}
                    <li>
                        <Link to='/contact' className="b" onClick={getGithubRepos}>Contact</Link>
                    </li>
                </ul>
            </div>
        </div>
    )
}

Navbar.propsType = {
    darkMode: PropTypes.bool.isRequired,
    toggleDarkMode: PropTypes.func.isRequired,
    getGithubRepos: PropTypes.func.isRequired
}

const styles = {
    marginTop: '-20px',
    marginRight: '18px',
    marginBottom: '10px',
    textDecoration: 'underline',
    fontStyle: 'italic'
}


export default Navbar;
